import * as Yup from 'yup';
// swal
import swal from 'sweetalert';
import { m } from 'framer-motion';
import { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
import { CopyToClipboard } from 'react-copy-to-clipboard';
// @mui
import { Container, Typography, Button, Grid, Stack, Card, Box } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { PATH_DASHBOARD } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import { varFade } from '../../components/animate';
import useAuth from '../../hooks/useAuth';
import Iconify from '../../components/Iconify';
import axios from '../../utils/axios';
// components
import { FormProvider, RHFTextField, RHFSelect } from '../../components/hook-form';



export default function ManualBank() {
  const { themeStretch } = useSettings();
  const { setting } = useAuth();
  const [payment, SetPayment] = useState(false)
  const { enqueueSnackbar } = useSnackbar();
  const [code, Setcode] = useState('');
  const [bank, SetBank] = useState([]);
  const [bankname, Setbankname] = useState('');
  const AccessToken = window.localStorage.getItem('accessToken');
  const UpdateUserSchema = Yup.object().shape({
    account_number: Yup.string().required('Sender Account Number required'),
    amount: Yup.string().required('Amount Sent Required'),
  });



  const methods = useForm({
    resolver: yupResolver(UpdateUserSchema),
  });
  useEffect(() => {
    axios.get('https://api.paystack.co/bank').then(data => SetBank(data?.data?.data))
  }, [AccessToken]);
  const {
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/manualpayment/system/secure/${AccessToken}/secure`, {
        amount_sent: data.amount,
        bank_name: bankname,
        bank_code: code,
        account_number: data.account_number
      });
      swal('success', 'Payment on Process', 'success')
      SetPayment(false)
    } catch (error) {
      if (error.data?.message !== undefined) {
        enqueueSnackbar(error.data?.message, { variant: 'error' })
      } else if (error?.message !== undefined) {
        enqueueSnackbar(error?.message, { variant: 'error' })
      } else {
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })
      }
    }
  };

  const bankchange = async (data) => {
    Setbankname(data)
    const value = bank.filter((item) => item.name === data)
    if (value[0]) {
      Setcode(value[0].code)
    }
  }



  return (
    <Page title="Manual Funding">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading={'Manual Funding'}
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Habukhan System' },
          ]}
        />
        {setting?.setting?.bank_transfer === 1 && <>
          <Typography variant="overline" sx={{ mb: 3, display: 'block', color: 'text.secondary' }}>
            Make a transfer to this Account Number given to you and click on confirm payment when done.
            <m.div variants={varFade().inRight}>
              <Typography variant="body2" noWrap>
                Account Number: <b>{setting?.bank?.account_number}</b> <CopyToClipboard text={setting?.bank?.account_number} onCopy={() => enqueueSnackbar('Account Number Copied')}><Button title="copy"><Iconify icon="codicon:copy" sx={{ width: 20, height: 20, color: 'success.main' }} /></Button></CopyToClipboard>
              </Typography>
              <Typography variant="body2" noWrap>
                Bank Name: {setting?.bank?.bank_name}
              </Typography>
              <Typography variant="body2" noWrap>
                Account Name: <b>{setting?.bank?.account_name}</b>
              </Typography>
            </m.div>
          </Typography>
          <Button onClick={() => SetPayment(true)}>Confirm Payment </Button>
        </>}
        {payment && <>
          <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
            <Grid container spacing={5}>
              <Grid item xs={12} md={8}>
                <Stack spacing={3}>
                  <Card sx={{ p: 3 }}>
                    <Typography variant="h4">Sender Account Details</Typography>
                    <Box
                      sx={{
                        display: 'grid',
                        rowGap: 3,
                        columnGap: 2,
                        gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
                      }}
                    >
                      <RHFSelect name="bank_name" label="Sender Bank Name" onChange={(data) => bankchange(data.target.value)} value={bankname}>
                        <option value="" />
                        {bank.map((option) => (
                          <option key={option?.code} value={option?.name}>
                            {option?.name}
                          </option>
                        ))}
                      </RHFSelect>
                      <RHFTextField name="account_number" label="Sender Account Number" />
                      <RHFTextField name="amount" type="number" label="Amount Sent" />
                    </Box>
                    <Stack spacing={3} alignItems="flex-end" sx={{ mt: 3 }}>
                      <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                        Proceed
                      </LoadingButton>
                    </Stack>
                  </Card>


                </Stack>
              </Grid>
            </Grid>
          </FormProvider>
        </>}

      </Container>
    </Page>
  );
}
